.question {
    background: #FEFEFE;
    border-radius: 8px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.04);
    padding: 24px;

    & + .question {
        margin-top: 16px;
    }

    p {
        color: #29292e;
    }

    footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 24px;

        .user-info{
            display: flex;
            align-items: center;
            
            img{
                width: 32px;
                height: 32px;
                border-radius: 50%;
            }

            span{
                margin-left: 8px;
                color: #737380;
                font-size: 14px;
            }
        }

        button{
            border: 0;
            background: transparent;
            cursor: pointer;
            transition: 0.2s;
            &.like-button {
                display: flex;
                align-items: flex-end;
                color: #737380;
                gap: 5px;

                &.liked{
                    color: #835AFD;
                    svg path{
                        stroke: #835AFD;
                    }
                }
                &:hover{
                    color: #835AFD;
                    svg path{
                        stroke: #835AFD;
                    }
                }
            }
            &:hover{
               filter: brightness(0.6); 
            }
        }

    }

    &.answered { 
        background: #DBDCDD; 
    }

    &.highlighted{
        background: #F4F0FF;
        border: 1px solid #835AFD;

        footer .user-info span{
            color: #29292e
        }

    }
}